<template>
  <div class="container" @click="handleGallaryClick">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="item of imgs" :key="item">
          <img class="gallary-img" :src="item">
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observeParents: true,
        observer: true,
        loop: true
      }
    }
  },
  methods: {
    handleGallaryClick () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="stylus" scoped>
  .container >>> .swiper-container
    overflow : inherit

  .container
    display : flex
    flex-direction : column
    justify-content : center
    position : fixed
    left : 0
    right : 0
    top : 0
    bottom : 0
    background : #000
    z-index : 99
    .wrapper
      // overflow : hidden
      height : 0
      width : 100%
      padding-bottom : 100%
      .gallary-img
        width : 100%
      .swiper-pagination
        bottom : -1rem
        color : #fff
</style>



